.item {
    height: 80vh;
    background: linear-gradient(rgba(94, 189, 252, 0.45) 25%, rgba(239, 252, 213, 0.75) 100%);
}


.pokeballButton {
    border: none;
    cursor: pointer;
    outline: none;
    display: flex;
    align-items: center;
    background: none;
    padding: 2rem 5rem 1rem 5rem;
    background: var(--bg-gradient);
}

.pokeballImg {
    width: 2.5rem;
    height: 2.5rem;
    margin-right: 0.5rem;
}

.itemInfoImg {
    width: 10rem;
    margin: 2rem 0;
    background-color: rgba(27, 83, 186, 0.4);
    border-radius: 50%;
}

.itemTitle {
    margin: 1rem 0;
    font-size: 2rem;
    font-weight: bold;
}

.itemInfo {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 1rem 0 2rem 0;
}


.titleDescrip {
    font-weight: bold;
    background: linear-gradient(to right, rgba(0, 117, 190, 0.4) 30%, rgba(255, 204, 0, 0.65) 110%);
}

.itemDescrip {
    display: flex;
    flex-direction: column;
    background-color: rgba(94, 189, 252, 0.1);
    padding-bottom: 6rem;
}