.pokemon {
    height: 100vh;
    background: linear-gradient(rgba(94, 189, 252, 0.45) 25%, rgba(239, 252, 213, 0.75) 100%);
}


.pokeballButton {
    border: none;
    cursor: pointer;
    outline: none;
    display: flex;
    align-items: center;
    background: none;
    padding: 2rem 5rem 1rem 5rem;
    background: var(--bg-gradient);
}

.pokeballImg {
    width: 2.5rem;
    height: 2.5rem;
    margin-right: 0.5rem;
}

.pokemonInfo {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0;
}

.pokemonInfoImg {
    width: 10rem;
    margin: 2rem 0;
    border-radius: 50%;
    background: linear-gradient(to bottom, rgba(22, 214, 251, 0.6) 40%, rgba(255, 255, 255, 1) 100%);
}

.pokeballTitle {
    margin: 1rem 0 2rem 0;
    font-size: 2rem;
    font-weight: bold;
}