.header {
    display: grid;
    font-size: 1.2rem;
    font-weight: bold;
    padding: 10px;
    place-items: center;
    background: var(--bg-gradient);
    transition: all .5s ease-in-out;
}

.sticky {
    position: fixed;
    top: 0;
    width: 100%;
}



.input {
    background-color: rgba(121, 121, 121, 0.267);
    padding: 10px 10px;
    border-radius: 5px;
    margin: 10px 10px;
    border: none;
    width: 80vw;
}