.nav {
    margin-bottom: 5rem;
}

.listItem {
    display: flex;
    border-bottom: 3px solid #e4e4e4;
    padding: 10px;
    text-decoration: none;
    align-items: center;
}

.listItem> :first-child {
    margin: 15px;
}

.listItem> :last-child {
    margin: 0px;
}

.listItemIcon {
    width: 2.5rem;
    height: 2.5rem;
    object-fit: fill;
}

.listItemText {
    flex-grow: 2;
}

.listItemText> :first-child {
    background: -webkit-linear-gradient(rgba(124, 166, 252, 1) 25%,
            rgba(119, 235, 129, 1) 100%);
    background-clip: text;
    -webkit-text-fill-color: transparent;
    font-size: 1rem;
    display: block;
    margin-bottom: 1.1rem;
    font-weight: bold;
}

.listItemText> :last-child {
    color: grey;
    font-weight: bolder;
    font-size: 0.8rem;
    display: block;
}